<template>
  <div>
    <div class="cms-form-layout ">
      <cms-prompt>
        <li> 用户关注公众号时，发送的欢迎信息。</li>
      </cms-prompt>
      <div class="media-warpper">
        <div class="menu-msg-panel">
          <div class="menu-msg-select_detail">
            <ul class="menu-msg-select__ul">
              <li>
                <div class="wx-create">
                  <key-word-zone class="wx-create__item" @change="getKeyword">
                    <div class="wx-create__item">
                      <p ><img src="./../../../../assets/images/keyword.png" alt=""></p>
                      <p class="wx-create__title" style="color:rgb(96, 98, 102);">关键词</p>
                    </div>
                  </key-word-zone>
                </div>
              </li>
            </ul>
          </div>
        </div>
        <!-- 显示 -->
         <div  class="keyword-items" v-if="keyword.id&&keyword.id!=''" style="width:40%;line-height:26px;">
          <div>
            <span>规则名：</span>
            {{keyword.ruleName}}
          </div>
            <span>关键词：</span>
          <template v-for="(wordItem,wordIndex) in keyword.wechatReplyKeywordList">
            <el-tag size="mini" v-if="wordItem.wordkeyEq!==''" :key="wordIndex" style="margin-left:5px;">{{wordItem.wordkeyEq}}</el-tag>
            <el-tag size="mini" v-if="wordItem.wordkeyLike!==''" :key="wordIndex" style="margin-left:5px;">{{wordItem.wordkeyLike}}</el-tag>
          </template>
          <div>
            <span>回复类型：</span>
            <span v-if="keyword.msgType ==='image'">图片</span>
            <span v-if="keyword.msgType ==='text'">文本</span>
            <span v-if="keyword.msgType ==='news'">图文</span>
            <span v-if="keyword.msgType ==='voice'">语音</span>
            <span v-if="keyword.msgType ==='video'">视频</span>
            <span v-if="keyword.msgType ==='music'">音乐</span>
          </div>
        </div>
      </div>
      <div class="cms-form-footer">
        <cms-button @click="saveData()" v-color style="width:100px">保存</cms-button>
      </div>
    </div>
  </div>
</template>
<script>
import form from '@/mixins/form.js'
import keyWordZone from './../materialLibrary/components/wxKeyWordZone'
import {
  keyclickSave,
  keyclickGEt,
  keyclickDelete
} from '@/api/wechat/autoResponse.js'
var defaultData = {
  replyContentId: '',
  replyType: 1
}
export default {
  name: 'autoResponseList',
  mixins: [form],
  components: {
    keyWordZone
  },
  data() {
    return {
      formData: Object.assign({}, defaultData),
      keyword: {},
      allID: ''
    }
  },
  methods: {
    getKeyword(items) {
      this.keyword = items[0]
      this.formData.replyContentId = items[0].id
    },
    getFormData() {
      // 通过id获取表单信息
      keyclickGEt({ replyType: 1 }).then(res => {
        if (res.code === 200) {
          this.keyword = res.data.wechatReplyContent
          this.formData.replyContentId = res.data.replyContentId
        }
        // this.allID = res.data.wechatReplyContent.wechatReplyKeywordList[0].id
      })
    },
    saveData() {
      keyclickSave(this.formData).then(res => {
        if (res.code === 200) {
          this.$successMessage('保存成功')
        } else {
          this.$errorMessage(res.message)
        }
      })
    },
    deleteData() {
      // 包装一层删除的操作
      this.keyword = []
      keyclickDelete({ ids: [this.allID] }).then(res => {
        // 禁用
        if (res.code === 200) {
          this.$successMessage('删除成功')
        } else {
          this.$errorMessage('删除失败')
        }
      })
    }
  },
  created() {
    this.getFormData()
  }
}
</script>
<style scoped>
.addG {
  width: 60%;
  margin: 0 auto;
}
.el-form-item .detail {
  color: #666666;
  font-size: 13px;
}
.notes_title {
  color: #999999;
}
.notes {
  color: #666666;
}
.notes a {
  color: #169bd5;
}
.cms-form-layout {
  padding: 0;
}
.media-warpper {
  border: 0px solid #eeeeee;
  margin: 15px;
}
.wx-repalce__warpper {
  top: -6px !important;
}
</style>




